@import '../../initialStyles/vars';

.navigation {
  font-family: "Source Sans Pro Semibold";

  &__container {
    margin-bottom: 12px;
    padding-top: 28px; }

  &__menu-title {
    margin-bottom: 16px;
    padding-left: 28px;

    color: #5b6779;
    font-size: 12px;
    text-transform: uppercase;
    line-height: 12px; }

  &__menu-item {
    display: flex;
    justify-content: space-between;

    height: 36px;
    padding-left: 29px;

    font-size: 14px;
    line-height: 36px;
    list-style: none;

    &--active {
      background-color: #313d4f;
      pointer-events: none; } }

  &__new-invitations {
    height: 24px;
    padding: 1px 9px;
    margin-right: 30px;
    margin-top: 5px;
    border-radius: 12px;

    background-color: $notifications-color;

    color: #fff;
    line-height: 25px;
    font-size: 14px; }

  &__menu-item-link {
    text-decoration: none;
    color: #f2f2f3;
    transition: transform $transition-timer;
    &:hover {
      transform: scale(1.1); } }

  &__input-search-wrapper::after {
    content: "";
    display: inline-block;
    position: relative;
    top: 3px;
    right: 12px;

    height: 16px;
    width: 16px;

    background-image: url('../../images/search-icon/search.png'); }

  &__input-search {
    width: 190px;
    margin-left: 26px;
    border: none;
    border-bottom: 1px solid #313d4f;
    outline: none;

    background-color: #222c3c;

    color: #fff;
    line-height: 35px;

    &::placeholder {
      color: #5b6779;
      font-size: 14px; } } }
